.InstructionsView__canvasBackground {
  background-image: linear-gradient(to bottom, rgba(62, 59, 59, 0.589), rgba(255, 255, 255, 0.249), rgba(18, 16, 16, 0.625)), url(../../assets/canvasBackground.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.InstructionsView__header {
  position: absolute;
  display: grid;
  grid-template-columns: 33% 34% 33%;
  align-items: center;
  width: 100vw;
  height: 8vh;
  z-index: 30;
}

.InstructionsView__header--timer {
  margin: 0;
  padding: 0;
  left: -5vw;
  font-size: 2.0rem;
  color: white;
}

.InstructionsView__header button {
  margin: 0 5vw;
  font-size: 2.0rem;
  border-radius: 15px;
}

.InstructionsView__message--top {
  position: absolute;
  top: 7vh;
  font-size: 1.2rem;
  margin: 1.0rem;
  padding: 0.5rem 1.5rem;
  border-radius: 15px;
}

.InstructionsView__message--bottom {
  position: absolute;
  bottom: 2vh;
  font-size: 1.0rem;
  margin: 1.0rem;
  padding: 0.5rem 1.0rem;
  border-radius: 15px;
}



@media only screen and (min-width: 675px) {
  .InstructionsView__header--timer {
    font-size: 2.5rem;
  }
  
  .InstructionsView__header button {
    font-size: 2.5rem;
  }
  
  .InstructionsView__message--top {
    top: 10vh;
    font-size: 2.0rem;
    left: 0;
    right: 0;
    margin: auto 2.0rem;
    padding: 1.0rem 2.0rem;
  }
  
  .InstructionsView__message--bottom {
    font-size: 2.0rem;
    margin: 2.0rem;
    padding: 1.0rem 2.0rem;
  }
}

.InstructionsView__isDemo-span {
  font-size: 28px;
  position: relative;
  bottom: 6px;

  animation: loading-flash 0.55s ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes loading-flash {
  0% {
    color: white;
  }
  100% {
    color: rgba(128, 128, 128, 0.714);
  }
}